.productos-container{
    display: grid;
    grid-template-columns: 33% 33% 33%;
    gap: .5rem;
    list-style: none;
    justify-content: end;
    align-items: center;
    padding: .5rem;
    margin: 1rem 0;
    background-color: #ffffff61;
    width: 80%;
    border-radius: 15px;
}

.producto-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
    padding: 1rem;
    width: 100%;
    height: 100%;
    scale: 90%;
    transition: all .3s;
    background-color: #fff;
    box-shadow: 0px 0px 5px #000;

}

.producto-item>img{
    height: 50%;
    width: 90%;
}

.producto-item-text{
    height: 50%;
    padding: .5rem;
    text-align: center;
    width: 80%;
}

/* Mediaqueryes 350px */
@media screen and (min-width: 350px) {
    .productos-container{
        grid-template-columns: 1fr;
    }
    
    .producto-item{
        height: auto;
    }
}

/* Mediaqueryes 720px */
@media screen and (min-width: 720px) {
    .productos-container{
        grid-template-columns: 50% 50%;
    }
}

/* Mediaqueryes 1350px */
@media screen and (min-width: 1350px) {
    .productos-container{
        grid-template-columns: 33% 33% 33%;
    }
}
