.principio-index-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.principio-index-text {
    background-image: url('./../../../../public/img/Fondo-index/fondo-index.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 100%;
    height: 100dvh;
}

.principio-index-text>img {
    width: 40%;
}

.principio-index-text>p {
    color: #000000;
    background-color: #ffffff7e;
    font-size: 1.4rem;
    font-weight: 600;
    width: 30%;
    border-radius: 10px;
}

.principio-index-text>p>strong {
    color: #0a9c00;
}

.principio-index-text>a>button {
    padding: .7rem 1.3rem;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    background-color: #a33939ad;
    transition: all .3s;
}

.principio-index-text>a>button:hover {
    background-color: #5f0000e4;
}

.principio-index-text>a>button {
    font-weight: bold;
    color: #fff;
    text-decoration: none;
    font-size: 1.5rem;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .principio-index-text>img {
        width: 90%;
    }
    
    .principio-index-text>p {
        font-size: 1rem;
        width: 70%;
    }
}

/* Mediaquerys 720px */
@media screen and (min-width: 710px) {
    .principio-index-text>img {
        width: 40%;
    }
    
    .principio-index-text>p {
        font-size: 1.4rem;
        width: 60%;
    }
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    
    .principio-index-text>p {
        font-size: 1.7rem;
        width: 50%;
    }
}