.menus-page-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: end;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .menus-page-container{
        width: 100%;
        align-items: center;
    }
    
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    .menus-page-container{
        align-items: end;
    }
}