.navbar {
    position: fixed;
    z-index: 10;
    left: 0;
    display: flex;
    flex-direction: column;
    transition: all .4s;
    text-align: left;
    height: 100dvh;
    width: 17rem;
    gap: 2rem;
    padding: 1rem 1rem 0 3.7rem;
    background-color: #ffffff2d;
    transform: TranslateX(-3rem);
}

.navbar:hover {
    background-color: #ffffffa9;
    transform: TranslateX(0);
}

.navbar>picture>img {
    height: 3rem;
    opacity: 1;
}

.navbar-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 60%;
    gap: .5rem;
    overflow: hidden;
    transition: all .5s;
}

.navbar-item {
    display: flex;
    justify-content: left;
    align-items: center;
}

.navbar-item>a {
    height: 100%;
    color: #a33939;
    display: flex;
    justify-content: left;
    align-items: center;
    text-decoration: none;
    letter-spacing: 2px;
    font-size: 1.5rem;
    border-radius: 10px;
    font-weight: bold;
    letter-spacing: 2px;
}

.navbar-item>a:hover {
    color: #5f0000;
    text-decoration: underline;
}


/* Queryes */
@media screen and (min-width: 150px) and (max-width: 1350px) {
    .navbar {
        height: 5rem;
        width: 10rem;
    }

    .navbar:hover {
        height: 100dvh;
        width: 17rem;
    }

}