.filtros-container {
    padding: .5rem;
    margin: .5rem;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
}

.filtros-list {
    display: grid;
    grid-template-columns: 33% 33% 33%;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    width: 100%;
    transition: all 1s;
    padding: 1rem;
}

.filtros-item {
    display: flex;
    height: 10rem;
    border-radius: 10px;
    transition: all .3s;
    overflow: hidden;
    box-shadow: 0px 0px 5px #000;
    flex: 0 0 auto;
}

.filtros-item:hover {
    scale: 105%;
    box-shadow: 0px 0px 10px #000;
    background-color: #fff;
}

.filtros-item>img {
    width: 100%;
    transition: all .3s;
}

.filtros-item:hover img {
    opacity: .4;
}

.filtros-item-text {
    position: absolute;
    z-index: 1;
    opacity: 0;
    transition: all .3s;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    width: 21.5rem;
    height: 10rem;
}

.filtros-item-text {
    text-align: center;
}

.filtros-item:hover>.filtros-item-text {
    opacity: 1;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .filtros-container {
        margin: 0;
        width: 100%;
    }

    .filtros-list {
        gap: .5rem;
        padding: .5rem;
    }

    .filtros-item-text {
        font-size: .7rem;
        width: 6.6rem;
        height: 10rem;
    }

}

/* Mediaquerys 710px */
@media screen and (min-width: 710px) {
    .filtros-item-text {
        font-size: 1.2rem;
        width: 14rem;
        height: 10rem;
    }
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    .filtros-container {
        margin: 0;
        width: 80%;
    }

    .filtros-list {
        gap: .5rem;
        padding: .5rem;
    }

    .filtros-item-text {
        font-size: 1.2rem;
        width: 21.5rem;
        height: 10rem;
    }
}