.contacto-container {
    gap: 1rem;
    width: 100%;
    display: grid;
    grid-template-columns: 38% 38%;
    justify-content: end;
    align-items: center;
    text-align: center;
}

@media screen and (min-width: 350px) {
    .contacto-container {
        padding-right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}

@media screen and (min-width: 1350px) {
    .contacto-container {
        gap: 1rem;
        width: 100%;
        display: grid;
        grid-template-columns: 38% 38%;
        justify-content: end;
        align-items: center;
        text-align: center;
    }
}