.tarjeta-sucursal-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: left;
    gap: 1rem;
    border-radius: 10px;
    border: 1px dashed #000;
    padding: .5rem;
}

.mapas {
    width: 50%;
    height: 100%;
}

.mapas>iframe {
    border: none;
    border-radius: 5px;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .tarjeta-sucursal-container {
        flex-direction: column;
    }
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    .tarjeta-sucursal-container {
        flex-direction: row;
    }
}