.tarjetas-sucursales-container{
    padding: .5rem;
    margin: .5rem;
    width: 80%;
    display: grid;
    grid-template-columns: 49% 49%;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .tarjetas-sucursales-container {
        display: flex;
        flex-direction: column;
        margin-top: 2rem;
    }
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    .tarjetas-sucursales-container{
        display: grid;
    }
}