.contacto-redes-container {
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff61;
    border-radius: 15px;
    gap: 2rem;
}

.contacto-redes-container>h3 {
    font-size: 2rem;
}

.img-redes {
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.img-redes>a>img {
    width: 4rem;
    height: 4rem;
}

/* Mediaquerys 350px */
@media screen and (min-width: 350px) {
    .contacto-redes-container {
        padding: .5rem;
        margin: 1rem;
        width: 90%;
    }
}

/* Mediaquerys 1350px */
@media screen and (min-width: 1350px) {
    .contacto-redes-container {
        padding: 3rem .5rem;
        margin: 0;
        width: 90%;
        margin: 0 auto;

    }

    .form-contacto>p {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        text-align: center;
    }

}